import { NgModule } from "@angular/core";
import { RouterModule } from "@angular/router";
import { CodeShellModule } from "codeshell/codeShellModule";
import { SharedModule } from "../Shared/SharedModule";
import { Registry } from "codeshell/core";
import { AuthFilter, ResourceActions } from "codeshell/security";

import { ServerConfigBase } from "codeshell/core";
import { TranslateService } from "@ngx-translate/core";


@NgModule({
    declarations: [],
    exports: [],
    imports: [
		SharedModule,
        RouterModule.forChild([
{ path:"Roles", loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\Roles\\RolesModule')['RolesModule']);  }, function(e: any) {    reject({ loadChunkError: true, details: e });  });}) } },
	{ path:"Users", loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\Users\\UsersModule')['UsersModule']);  }, function(e: any) {    reject({ loadChunkError: true, details: e });  });}) } },
	
		])
    ]
})
export class AuthModule {

    constructor(trans: TranslateService, conf: ServerConfigBase) {
        trans.setDefaultLang(conf.Locale);
        trans.use(conf.Locale);
    }
}

